<template>
  <el-dialog
    width="600px"
    title="多库位生成"
    :visible.sync="isVisible"
    @close="cancel"
  >
    <div class="org-form">
      <el-form :inline="true" ref="orgForm" :model="orgForm" :rules="orgFormRules" label-width="180px">
        <el-row>
        <el-col :span="18">
        <el-row>
          <el-col>
            <el-form-item label="库位" prop="storageTitleBegin">
              <el-input clearable v-model="orgForm.storageTitleBegin" style="width:65px"/>
            </el-form-item>
            <span class="mid-text">到</span>
            <el-form-item>
              <el-input clearable :span="3" v-model="orgForm.storageTitleEnd" style="width:65px"/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="列数" prop="storageColumn">
              <el-input clearable v-model="orgForm.storageColumn" style="width:65px"/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="层数" prop="storageFloors">
              <el-input clearable v-model="orgForm.storageFloors" style="width:65px"/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
            <el-col>
              <el-form-item label="每个库位长">
                <el-input clearable v-model="orgForm.length" style="width:65px"/>
              </el-form-item>
              <span class="mid-text">米，宽</span>
              <el-form-item>
                <el-input clearable v-model="orgForm.width" style="width:65px"/>
              </el-form-item>
              <span class="mid-text">米</span>
            </el-col>
        </el-row>
        </el-col>
        <el-col :span="6">
          <el-row>
            <el-button class="mar-t-30" type="primary" @click="save">生成</el-button>
          </el-row>
          <el-row>
            <el-button class="mar-t-30" @click="reset">重置</el-button>
          </el-row>
        </el-col>
        </el-row>
      </el-form>
    </div>
  </el-dialog>
</template>

<script>
  import { createMoreShelves } from '@/api/warehouse'
  export default {
    name: 'CreateMore',
    data () {
      return {
        isVisible:false,
        orgForm: {
          storageTitleBegin: '',
          storageTitleEnd: '',
          storageColumn: '',
          storageFloors: '',
          length: '',
          width: ''
        },
        orgFormRules: {
          storageTitleBegin: [
            {required: true, message: '请填写内容', trigger: 'blur'}
          ],
          storageColumn: [
            {required: true, message: '请填写内容', trigger: 'blur'},
          ],
          storageFloors: [
            {required: true, message: '请填写内容', trigger: 'blur'},
          ]
        }
      }
    },
    props: {
      show: {
        type: Boolean,
        required: true,
        default: false
      },
      selectedId: {
        type: String,
        required: true
      }
    },
    mounted () {
    },
    methods: {
      cancel () {
        this.isVisible = false
        this.resetData()
        this.$refs.orgForm.resetFields()
      },
      save () {
        this.$refs.orgForm.validate(isValid => {
          let params = {
            ...this.orgForm,
            wareHouseUuid: this.selectedId
          }
          if (isValid) {
            createMoreShelves(params).then(res => {
              if (!res.error) {
                this.$message({
                  message: `生成成功!`,
                  type: 'success'
                })
                this.$emit('updateTree')
                this.cancel()
              }
            })
          }
        })
      },
      reset () {
        this.resetData()
        this.$refs.orgForm.resetFields()
      },
      resetData () {
        this.orgForm = {}
      }
    },
    watch: {
      show (isShow) {
        this.isVisible = isShow
      }
    }
  }
</script>

<style lang="scss" scoped>
.mid-text{
  display: inline-block;
  height: 29px;
  line-height: 29px;
}
</style>



