<template>
  <div class="shelves">
    <div class="main">
      <div class="left-tree">
        <div class="warehouseList">
          <span>仓库</span>
          <el-select v-model="selectedId" filterable @change="updateTree">
            <el-option
              v-for="(org, index) in warehouseList"
              :key="index"
              :label="org.warehouseName"
              :value="org.warehouseUuid"></el-option>
          </el-select>
        </div>
        <div class="warehouseList">
          <span>商户</span>
          <el-select v-model="selectedId" filterable @change="updateTree">
            <el-option
              v-for="(org, index) in warehouseList"
              :key="index"
              :label="org.warehouseName"
              :value="org.warehouseUuid"></el-option>
          </el-select>
        </div>
        <VuePerfectScrollbar class="scroll-area" >
          <tree
            v-if="showTree"
            ref="tree"
            :selectedId="selectedId"
            :clickedNodeData="clickedNodeData"
            :isCreate.sync="isCreate"
            :shelveTreeData="shelveTreeData"
            @nodeClick="handleNodeClick"
            @openDialog="openDialog"
            @deleteNode="deleteNode"
            @updateTree="updateTree"
            @openCreateMoreDialog="openCreateMoreDialog">
          </tree>
        </VuePerfectScrollbar>
      </div>
      <div class="shelves-content">
        <shelves-detail
          ref="shelvesDetail"
          :selectedId="selectedId"
          :isCreate.sync="isCreate"
          :nodeData="clickedNodeData"
          @tableUpdate="updateTree"
          @openDialog="openDialog"
          @deleteShelvesList="deleteShelvesList"
        />
      </div>
    </div>
    <!-- 新增库位-->
    <shelves-create
      :selectedId="selectedId"
      :show.sync="showUpdateDialog"
      :isCreate.sync="isCreate"
      :nodeData="selectedData"
      @updateTree="updateTree"
    />
    <!-- 多库位生成 -->
    <create-more
      :selectedId="selectedId"
      :show.sync="showCreateMoreDialog"
      @updateTree="updateTree"
    >
    </create-more>
  </div>
</template>

<script>
import Tree from './Components/Tree'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import { generateTree } from '@/utils/generateTree'
import { getShelvesTree } from '@/api/warehouse'
import ShelvesDetail from './Components/ShelvesDetail'
import ShelvesCreate from './Components/ShelvesCreate'
import CreateMore from './Components/CreateMore'
import { getWarehouseList, deleteShelves, deleteShelvesList } from '@/api/warehouse'
import { setSession, getSession } from '@/utils/session'
import {mixins} from '@/mixins'

export default {
  name: 'ShelvesManager',
  mixins: [mixins],
  data () {
    return {
      warehouseList: this.$store.getters.warehouseList,
      shelveTreeData:[],
      selectedId: '',
      clickedNodeData: {},
      selectedData: {},
      isCreate: false,
      showUpdateDialog: false,
      showCreateMoreDialog: false,
      showTree: false
    }
  },
  mounted () {
      this.showTree = true
      this.selectedId = getSession('warehouseUuid') ?  getSession('warehouseUuid') :this.warehouseList[0].warehouseUuid
      this.updateTree()
  },
  methods: {
    handleNodeClick ({data, node, nodeCom}) {
      this.clickedNodeData = data
    },
    updateTree () {
      setSession('warehouseUuid',this.selectedId)
        const params = {
          storageWarehouseUuid: this.selectedId
        }
        getShelvesTree(params).then(res => {
          this.shelveTreeData = generateTree(res)
        })
    },
    openDialog (nodeData) {
      this.selectedData = nodeData
      this.showUpdateDialog = true
    },
    openCreateMoreDialog () {
      this.showCreateMoreDialog = true
    },
    //删除节点
    deleteNode (nodeData) {
      this.$confirm('该库位下有子库位，确认删除吗?', '提示', {
        type: 'warning'
      }).then(() => {
        let params = {
          storageUuid: nodeData.id
        }
        deleteShelves(params).then(res => {
          if(!res.error){
            this.$message({
              message: `删除成功`,
              type: 'success'
            })
            this.updateTree()
          }
        })
      }).catch(() => {})
    },
    deleteShelvesList (params) {
      deleteShelvesList(params).then(res => {
        if(!res.error){
          this.$message({
            message: '删除成功',
            type: 'success'
          })
          this.updateTree()
        }
      })
    }
  },
  components: {
    Tree,
    VuePerfectScrollbar,
    ShelvesDetail,
    ShelvesCreate,
    CreateMore
  }
}
</script>

<style lang="scss" scoped>
.shelves{
  width:100%;
  height:100%;
  display: flex;
  flex-direction: column;
  .main {
    display: flex;
    height:100%;
    flex-direction: row;
    .warehouseList{
      margin: 10px 10px;
    }
    .left-tree {
      min-width:230px;
      padding:15px 0;
      background: #fff;
      border-right:2px solid darken($base-light-color, 5%);
      margin-bottom: 20px;
      .scroll-area {
        width:100%;
        height:100%;
      }
    }

    .shelves-content {
      flex:1 auto;
    }
  }

}
</style>



