<template>
  <el-dialog
    width="670px"
    :title="(isCreate ? '新增':'编辑')+'库位'"
    :visible.sync="isVisible"
    @close="cancel"
  >
    <div class="org-form">
      <el-form :inline="true" ref="orgForm" :model="orgForm" :rules="orgFormRules" :disabled="isFormDisabled" label-width="100px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="库位名称" prop="storageName">
              <el-input v-model="orgForm.storageName" clearable/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="类型" prop="storageType">
              <el-input v-model="orgForm.storageType" clearable />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="编号" prop="storageId">
              <el-input v-model="orgForm.storageId" clearable />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="容量" prop="storageCapacity">
              <el-input v-model="orgForm.storageCapacity" clearable/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="长度" prop="storageLength">
              <el-input v-model="orgForm.storageLength" clearable/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="宽度" prop="storageWidth">
              <el-input v-model="orgForm.storageWidth" clearable/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="状态" prop="orgEnabled">
              <el-radio-group v-model="orgForm.storageStatus" :value="orgForm.storageStatus">
                <el-radio :label="1">启用</el-radio>
                <el-radio :label="0">禁用</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="描述" prop="storageDesc" style="width:100%">
              <el-input type="textarea"
                        style="width:495px"
                        maxlength="200"
                        v-model="orgForm.storageDesc"
                        :rows="5"
                        placeholder="填写库位描述,最长200字符"
                        clearable
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24" class="flex-right">
            <el-button type="primary" @click="save">保存</el-button>
            <el-button @click="cancel">取消</el-button>
          </el-col>
        </el-row>
      </el-form>
    </div>
  </el-dialog>
</template>
<script>
// api
import { getOrgList } from '@/api/common'
import { updateShelves, deleteShelves } from '@/api/warehouse'

// init Form Data
const initFormState = {
  storageStatus: '1'
}

export default {
  name: 'ShelvesCreate',
  data () {
    return {
      showDetailMode: true,
      isVisible: false,
      isFormDisabled: false,
      organization: [],
      orgForm: Object.assign({}, initFormState),
      orgFormRules: {
        storageName: [
          {required: true, message: '请填写库位名称', trigger: 'blur'},
          {max: 30, message: '最大长度为30字符', trigger: 'blur'}
        ],
        storageType: [
          {required: true, message: '请填写库位类型', trigger: 'blur'},
          {max: 30, message: '最大长度为30字符', trigger: 'blur'}
        ],
        storageId: [
          {required: true, message: '请输入库位编号', trigger: 'blur'},
          {max: 30, message: '最大长度为30字符', trigger: 'blur'}
        ]
      }
    }
  },
  props: {
    show: {
      type: Boolean,
      required: true,
      default: false
    },
    nodeData: {
      type: Object,
      required: true
    },
    isCreate: {
      type: Boolean,
      required: true
    },
    selectedId: {
      type: String,
      required: true
    }
  },
  watch: {
    show (isShow) {
      if (isShow) {
        // 新增
        if (this.isCreate) {
          this.setToCreateState()
          // 编辑
        } else {
          this.setToEditState()
        }
      }

      // 设置窗口显示状态
      this.isVisible = isShow
    }
  },
  methods: {
    setToCreateState () {
      const parentNode = this.nodeData
      if (parentNode) {
        this.orgForm = Object.assign({}, initFormState)
        this.orgForm.storagePid = this.nodeData.id
      } else {
        this.$message({
          message: '未找到父节点,无法在此情况下创建组织结构.',
          type: 'error'
        })
        this.isFormDisabled = true
      }
    },
    setToEditState () {
      this.orgForm = Object.assign({}, this.nodeData)
    },
    save () {
      this.$refs.orgForm.validate(isValid => {
        if (isValid) {
          const submitText = this.isCreate ? '新增' : '修改'
          let params = {
            storageWarehouseUuid: this.selectedId,
            isCreate: this.isCreate,
            ...this.orgForm
          }
          console.log(params)
          updateShelves(params).then(res => {
            if (!res.error) {
              this.$message({
                message: `${submitText}成功!`,
                type: 'success'
              })
              // 重新读取tree
              this.$emit('updateTree')
              this.cancel()
            }
          })
        }
      })
    },
    cancel () {
      this.isVisible = false
      this.$emit('update:show', false)
      this.orgForm = Object.assign({}, initFormState)
      this.$refs.orgForm.resetFields()
    }
  }
}
</script>
<style lang="scss" scoped>
  .org-content {
    display: flex;
    flex-direction: column;
    align-items: center;

    .org-form{
      width:80%;
      max-width:1000px;
      min-width:600px;
      margin:10px;
      padding:10px;
      border:1px solid $base-light-color;
      border-radius:8px;
    }
  }
</style>



